import anime from 'animejs/lib/anime.es.js';
import * as tome from 'chromotome';

import Engine from './utils/engine';
// import Engine from './utils/engine3d';

import { randomBetween } from './utils/utils';

import Vector from './utils/vector';

const WIDTH = 480;
const HEIGHT = 480;

const rows = 10;
const cols = 10;

const pad = 50;

const el = document.getElementById('canvas');
// const el = document.getElementById('container');

let palette = tome.getRandom();

const app = new Engine(el, {
  debug: false,
  width: WIDTH,
  height: HEIGHT
});

const posArray = new Array(rows * cols).fill(0).map(() => {
  return {
    x: 0,
    y: -1,
    color: palette.colors[Math.floor(randomBetween(0, palette.colors.length))],
    fill: Math.random() > 0.5
  };
});

anime({
  targets: posArray,
  keyframes: [
    { y: 0 },
    { y: 1, delay: 500 }
  ],
  delay: 1000,
  duration: 4000,
  easing: 'easeInOutQuart',
  delay: anime.stagger(100, {grid: [rows, cols], from: 'center'}),
  loopComplete: function(anim) {
    palette = tome.getRandom();
    posArray.forEach(p => {
      p.color = palette.colors[Math.floor(randomBetween(0, palette.colors.length))];
      p.fill = Math.random() > 0.5
    })
  },
  loop: true
});

const drawCircle = (x, y, size, { y: circleY, color, fill }) => {
  ctx.save();
  ctx.strokeStyle = color;
  ctx.fillStyle = color;
  ctx.translate(x, y);

  ctx.beginPath();
  ctx.rect(0, 0, size, size);
  ctx.clip();

  ctx.beginPath();
  ctx.arc(size / 2, (size / 2) + (circleY * size), size * 0.2, 0, Math.PI * 2);

  if (fill) {
    ctx.fill();
  }
  ctx.stroke();

  ctx.restore();
}

function drawGrid(x, y, width, height) {
  const size = width / cols;

  ctx.save();
  ctx.translate(x, y);

  for (let x = 0; x < cols; x++) {
    for (let y = 0; y < rows; y++) {
      let idx = (y * cols) + x;
      drawCircle(x * size, y * size, size, posArray[idx]);
    }
  }

  ctx.restore();
}

app.onRender(({ frame, width, height, debug }) => {
  ctx.clearRect(0, 0, width, height);

  drawGrid(pad, pad, width - (pad * 2), height - (pad * 2));
});

app.start();
